.navBar {
    background-color: white;
    padding: 2rem 5rem;
  }
  
  .navBar header {
    position: relative;
    display: inline-block;
    left: 25%;
    text-align: center;
    font-size: 20px;
  }
  
  .navBar button {
    width: 10%;
    border-color: transparent;
    background-color: transparent;
    font-size: 15px;
  }
  .navBar button:hover {
    color: rgb(196, 160, 0);
  }
  
  .navBar i {
    opacity: 0.5;
    padding: 5px;
  }
  
  .navBar .btn_right {
    float: right;
  }
  
  #logout {
    height: 31px;
  }