.popup {
    display: flex;
    flex-wrap: wrap;
    position: fixed;
    width: 70%;
    top: 5rem;
    left: 15%;
    padding: 5rem;
    background-color: white;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 2;
  }
  
  .popup img {
    width: 50%;
    margin-right: 2rem;
  }
  
  .popup_text {
    width: 40%;
  }
  
  .popup_text button {
    padding: 1rem;
    border: 1px solid rgb(56, 56, 56);
    background-color: rgb(56, 56, 56);
    color: white;
  }
  
  .close {
    position: fixed;
    right: 270px;
    top: 140px;
    font-size: 30px;
    background-color: transparent;
    border-style: hidden;
    transition: ease 0.3s;
  }
  
  .close:hover {
    transform: scale(1.3);
  }
  
  .overlay {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
  }