.cart {
    margin: 0rem 5rem 3rem 5rem;
    height: 100%;
  }
  
  .cart h1,
  .cart_total h2 {
    font-size: 25px;
  }
  
  .cart_banner {
    background-color: rgb(248, 249, 250);
    padding: 5rem 3rem;
  }
  
  .cart_banner header {
    display: inline;
    font-size: 30px;
  }
  
  .cart p,
  .cart_banner p {
    display: inline;
    float: right;
    margin-bottom: 0px;
    opacity: 0.5;
    font-size: 15px;
  }
  
  .cart_items {
    display: inline-block;
    width: 65%;
    min-height: 23rem;
    margin-right: 2%;
    border-spacing: 0px;
  }
  
  .name {
    font-weight: bold;
  }
  
  .cart_items thead,
  .cart_items tfoot {
    background-color: rgb(248, 249, 250);
  }
  
  .cart_items th {
    padding: 1rem 0.5rem;
  }
  
  .cart_items td {
    text-align: center;
    padding: 0.5rem;
  }
  
  .cart_items tfoot td {
    padding: 1rem;
  }
  
  .cart_items button {
    margin: 1px;
    border: solid 0.5px transparent;
    border-radius: 0px;
    background-color: transparent;
  }
  
  .cart_items #left {
    float: left;
    padding: 0.5rem;
  }
  
  .cart_items #right {
    float: right;
    padding: 0.5rem;
  }
  
  .cart_items #left:hover,
  .cart_items #right:hover {
    border: solid 1px black;
  }
  
  .cart_imgs {
    width: 10rem;
  }
  
  .cart_total {
    display: block;
    width: 32%;
    float: right;
    padding: 2rem;
    background-color: rgb(248, 249, 250);
  }
  
  .cart h1,
  .cart_total h2,
  .cart_total h3 {
    font-weight: normal;
  }
  
  .cart_total h3,
  .cart_total p {
    display: inline-block;
    margin-top: 0rem;
  }
  
  .cart_total p {
    float: right;
  }
  
  .cart_total input,
  .cart_total button {
    display: block;
    width: 100%;
  }
  
  .cart_total input {
    margin-top: 2rem;
    padding: 1rem;
    width: 20rem;
    border-radius: 0px;
    border: 1px solid rgb(228, 230, 233);
  }
  
  .cart_total button {
    padding: 1rem;
    border: 1px solid rgb(56, 56, 56);
    background-color: rgb(56, 56, 56);
    color: white;
  }
  
  #price_total {
    opacity: 1;
    font-size: 19px;
  }